
.calculator button {
    width: 25%;
    height: 40px;
    border: none;
    cursor: pointer;
    transition: all .2s;
    outline: 1px solid rgba(0,0,0, .1);
  }

  button.equal{
    width: 50%;
  }
  
  .calculator button:hover {
    opacity: .9;
  }
  
  .calculator button:active {
    opacity: 1;
  }